.move-modal .modal-overlay {
  backdrop-filter: blur(4px);
  animation: fadeIn 0.3s ease-out;
}
.move-modal .modal-content {
  animation: slideUp 0.3s ease-out;
}
.move-modal .folder-tree .folder-tree-item {
  transition: all 0.2s ease;
}
.move-modal .folder-tree .folder-tree-item:hover {
  background-color: #f8fafc;
}
.move-modal .folder-tree .folder-tree-item.selected {
  background-color: #eff6ff;
  border-left: 3px solid #3b82f6;
}
.move-modal .folder-tree .folder-tree-item .folder-icon {
  transition: transform 0.2s ease;
}
.move-modal .folder-tree .folder-tree-item:hover .folder-icon {
  transform: scale(1.1);
}
.move-modal .destination-info {
  animation: slideDown 0.2s ease-out;
  background: linear-gradient(135deg, #eff6ff, #dbeafe);
}
.move-modal .move-button {
  transition: all 0.2s ease;
}
.move-modal .move-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}
.move-modal .move-button:active {
  transform: translateY(0);
}
.move-modal .cancel-button {
  transition: all 0.2s ease;
}
.move-modal .cancel-button:hover {
  background-color: #e5e7eb;
}
.move-modal .close-button {
  transition: all 0.2s ease;
}
.move-modal .close-button:hover {
  color: #6b7280;
  transform: scale(1.1);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
